.header__mid:after { content: " "; display: table; clear: both; }

.header__mid-col { display: inline-block; vertical-align: top; width: 50%; }

.header__nav { position: relative; z-index: 10; text-align: right; }

.header__nav .nav__list { position: relative; padding-right: 10px; height: 90px; }

.header__top-links { position: relative; margin-top: 20px; z-index: 1; }

.header__search { display: none; padding: 5px 15px; background: #09A1D9; }

@media only screen and (max-width: 424px) { .header__search { padding: 5px 10px; } }

.header__breadcrumbs { position: relative; padding: 5px 0; z-index: 1; }

.search__field { position: relative; }

.search__input { padding: 0 39px 0 7px; font-size: 15px; line-height: 13px; height: 31px; width: 100%; font-family: "PT Sans", Arial, sans-serif; font-size: 17px; font-weight: 400; background-color: #fff; border: 1px solid transparent; color: #000; transition: 0.2s cubic-bezier(0.55, 0.09, 0.68, 0.53); }

.search__input::placeholder { color: #B3B3B3; }

.search__input:focus { border-color: #0076D1; }

.search__input:focus + .search__btn:hover { border-color: #0076D1; }

.search__input.is-active + .search__btn { border-color: #0076D1; }

.search__input.is-active + .search__btn:hover { background: #D6D6D6; }

@media only screen and (max-width: 424px) { .search__input { font-size: 16px; } }

.search__btn { position: absolute; top: 0; right: 0; bottom: 0; width: 41px; height: 41px; background-color: #F5F5F5; border: 2px solid #fff; transition: .3s; }

@media only screen and (max-width: 1599px) { .search__btn { width: 33px; height: 31px; } }

.search__btn::after { display: block; margin: 0 auto; content: ''; background-image: url("../img/sprite.png"); width: 18px; height: 16.66667px; background-position: -40.66667px -162px; background-size: 223.33333px 192px; transform: scale(1.1); }

.search__btn:active { background-color: #D6D6D6; }

.top-links { font-size: 0; text-align: center; }

.top-links__link { display: inline-block; margin-bottom: 5px; width: calc(50% - 5px); padding: 0 5px; height: 38px; line-height: 33px; color: #fff; border: 2px solid rgba(255, 255, 255, 0.5); font-size: 18px; transition: 0.2s cubic-bezier(0.55, 0.09, 0.68, 0.53); }

@media only screen and (max-width: 424px) { .top-links__link { font-size: 16px; width: calc(50% - 3px); } }

.top-links__link:nth-child(2n) { margin-left: 10px; }

@media only screen and (max-width: 424px) { .top-links__link:nth-child(2n) { margin-left: 5px; } }

.top-links__link:hover { background-color: rgba(56, 87, 148, 0.3); border-color: #348EBF; }

.cart { position: relative; }

.cart:hover .cart__icon { fill: #666666; }

.cart__icon { font-size: 40px; fill: #999999; }

.cart__total { position: absolute; min-width: 38px; right: -15px; bottom: -10px; padding: 2px 4px; border: 1px solid #999999; border-radius: 5px; font-family: "Open Sans", Arial, sans-serif; font-size: 15px; line-height: 1; font-weight: 600; color: #4D4D4D; text-align-last: center; white-space: nowrap; background: #fff; }

.cart__val-icon { fill: currentColor; font-size: 11px; }

.menu { display: none; position: relative; z-index: 2; }

.menu__list { position: relative; font-size: 0; text-align: center; }

.menu__item { position: relative; z-index: 2; display: block; margin-right: 6px; vertical-align: top; width: 100%; text-align: center; }

.menu__link { display: block; padding: 9px 10px; background-color: #EDFAFF; border-bottom: 2px solid #48A6DD; font-size: 18px; color: #000; }

@media only screen and (max-width: 424px) { .menu__link { font-size: 16px; } }

.menu__link:hover { background-color: #F2FBFF; border-bottom-color: #F8B772; }

.menu__link:active { background-color: #A8DEFF; border-bottom-color: #F18F82; }

.menu__new { position: absolute; top: 2px; right: 50%; transform: translateX(200%); z-index: 1; color: #ff0000; font-size: 14px; pointer-events: none; }

@media only screen and (max-width: 424px) { .menu__new { font-size: 13px; } }

.footer { padding: 20px 0; background-color: #002757; background-image: linear-gradient(to top, #002757 0%, rgba(0, 62, 138, 0.65) 100%); color: #fff; overflow: hidden; }

.footer__top { *zoom: 1; }

.footer__top:after { content: " "; display: table; clear: both; }

.footer__top-col { position: relative; }

.footer__top-col:nth-child(1) { float: left; width: 55%; }

.footer__top-col:nth-child(2) { float: right; top: 45%; }

.footer__address { margin-top: 25px; position: relative; z-index: 10; font-style: normal; *zoom: 1; }

.footer__address:after { content: " "; display: table; clear: both; }

@media only screen and (max-width: 424px) { .footer__address { font-size: 16px; } }

.footer__address-row { max-width: 310px; margin: 0 auto 10px; padding: 0 5px 0 25px; transform: translateX(30px); }

.footer__address-tel { display: inline-block; width: 100%; margin-bottom: 3px; }

.footer__address-descr { margin-bottom: 3px; }

.footer__address-str { position: relative; margin-bottom: 3px; }

.footer__address .icon { position: absolute; top: 5px; left: -21px; z-index: 1; fill: #CFE7F9; font-size: 31px; }

@media only screen and (max-width: 1599px) { .footer__address .icon { font-size: 24px; } }

.footer__copy { margin-top: 10px; text-align: center; color: #CFE7F9; font-size: 15px; }

.footer__copy-text { margin: 10px 0 0; }

.footer__copy-link { color: #CFE7F9; text-decoration: underline; text-decoration-color: #CFE7F9; font-size: 18px; }

@media only screen and (max-width: 424px) { .footer__copy-link { font-size: 16px; } }

.footer__copy-link:hover { text-decoration: none; }
