.request {
	padding: 20px 0 30px;
	background-color: #ececec;
	&__title {
		margin-bottom: 20px;
		text-align: center;
		font-size: 19px;
		@include sm {
			margin-bottom: 15px;
			font-size: 18px; } }
	&__descr {
		text-align: center;
		font-size: 17px;
		@include sm {
			font-size: 16px; } }
	&__link {
		display: block;
		color: #1166ab;
		span {
			border-bottom: 1px solid #1166ab; } }
	&__wrap {
		margin: 30px 0 0;
		@include sm {
			margin: 20px 0 0; } }
	&__field {
		position: relative;
		width: 100%;
		border: 1px solid #5d8dcd;
		overflow: hidden;
		margin-bottom: 15px;
		&::after {
			position: absolute;
			left: -1px;
			top: -2px;
			z-index: 1;
			content: '';
			@include md {
				left: -6px;
				top: -4px;
				transform: scale(.8); } }
		&_name::after {
			@include s('user_form');
			left: -8px!important; }
		&_tel::after {
			@include s('phone_form'); }
		&_email::after {
			@include s('mail_form'); } }
	&__input {
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		padding: 0 5px 0 50px;
		height: 30px;
		line-height: 30px;
		background-color: #fff;
		@include f("ver",15);
		@include placeholder-color(#999999);
		@include sm {
			font-size: 14px; } }
	&__message-wrap {
		position: relative;
		padding-left: 40px;
		border: 1px solid #5d8dcd;
		background-color: #f3f4f4;
		overflow: hidden;
		&::after {
			content: '';
			position: absolute;
			z-index: 1;
			top: -6px;
			left: -7px;
			transform: scale(.75);
			@include s('dots_form'); } }

	&__message {
		display: block;
		padding: 6px 12px 12px 8px;
		min-height: 130px;
		width: 100%;
		background-color: #fff;
		resize: none;
		border: none;
		@include f("ver",15);
		@include placeholder-color(#999999);
		@include tr(min-height .3s);
		@include sm {
			font-size: 14px; }
		&.extended {
			min-height: 190px; }
		&-extend {
			position: absolute;
			width: 65px;
			height: 29px;
			background-color: #f7f7f7;
			bottom: 9px;
			right: 7px;
			line-height: 43px; }
		&-extend.is-active &-icon {
				transform: scaleY(-1); }
		&-extend:hover &-icon {
				fill: #56708A; }
		&-extend:active &-icon {
				fill: #103559; }
		&-icon {
			font-size: 21px;
			fill: #C4C4C4; } }
	&__send {
		margin-top: 20px;
		font-size: 0; }
	&__send-row {
		font-size: 0;
		& + & {
			text-align: center;
			margin-top: 20px; } }
	&__send-btn {
		padding: 0 73px 0 5px;
		min-width: 220px;
		font-size: 18px;
		line-height: 45px;
		height: 45px; }
	&__send-add {
		display: inline-block;
		vertical-align: top;
		margin-right: 15px;
		white-space: nowrap; }
	&__send-btn-add {
		display: inline-block;
		vertical-align: middle;
		padding: 0 5px;
		max-width: 128px;
		min-height: 114px;
		color: #fff;
		background-color: #80C9E8;
		white-space: normal;
		@include f("ver",16);
		text-transform: uppercase;
		border-bottom: 1px solid #A8B3BF;
		@include lg {
			font-size: 13px;
			max-width: 108px;
			min-height: 96px; }
		&::after {
			content: '';
			display: block;
			margin: 3px auto 0;
			@include sr('file');
			transform: scale(.9);
			@include md {
				transform: scale(.8); } }
		&:hover::after {
			@include sr('file-hvr'); }
		&:active::after {
			@include sr('file-act'); }
		&:hover {
			background: #5EAFDA;
			border-bottom-color: #FFFF00; }
		&:active {
			background: #2A578A;
			border-bottom-color: #E63E2D; } }
	&__send-files {
		margin-bottom: 10px;
		text-align: left; }
	&__send-item {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: 110px;
		height: 98px;
		margin-right: 15px;
		margin-bottom: 15px;
		@include vertical;
		&:last-child {
			margin-right: 0; }
		&:nth-child(2n) {
			margin-right: 0;
			@include xs {
				margin-right: 20px; } }
		&:nth-child(2) ~ & {
			margin-top: 20px;
			@include xs {
				margin-top: 0; } }
		&:nth-child(3) ~ & {
			@include xs {
				margin-top: 20px; } }
		img {
			display: inline-block;
			vertical-align: middle;
			width: 100%;
			height: 100%;
			object-fit: contain; }
		.decor__line {
			@include md {
				width: 82%; } }
		.decor__line::before {
			@include md {
				border-width: 0 0 16px 15px;
				right: -5px; } }
		.decor__line::after {
			@include md {
				border-width: 15px 15px 0 0; } } }
	&__send-btn-close {
		position: absolute;
		right: -15px;
		top: -15px;
		width: 30px;
		height: 30px;
		border-radius: 50%;
		background: #E63833;
		border: 3px solid #fff;
		@include tr(background .2s);
		@include md {
			width: 24px;
			height: 24px;
			border-width: 2px; }
		&:hover {
			background: #3e5b8a; } }
	&__send-icon-close {
		fill: #fff;
		@include vcenter;
		font-size: 12px;
		@include md {
			font-size: 10px; } }
	&__send-message {
		@include f("ver",20);
		@include md {
			font-size: 15px; }
		@include sm {
			font-size: 14px; }
		&::before {
			content: '';
			display: inline-block;
			vertical-align: -30px;
			transform: scale(.7);
			@include st('mark');
			@include md {
				transform: scale(.4); } } } }
