// home sld
.home-slider {
	display: none;
	position: relative;
	z-index: 3;
	&.slick-initialized {
		display: block; }
	&:hover &__btn {
 }		//opacity: 0
	&__item {
		position: relative;
		height: 160px;
		img {
			width: 100%;
			height: 100%; } }
	&__label {
		position: absolute;
		min-height: 25px;
		min-width: 210px;
		padding: 0 5px;
		line-height: 25px;
		z-index: 2;
		top: 2px;
		left: 50%;
		transform: translateX(-50%);
		@include f('tcm',16);
		color: #125082;
		text-align: center;
		text-transform: uppercase;
		background-color: #ECF7FD;
		@include sm {
			font-size: 14px; }
		@include trs;
		&:hover {
			background: darken(#ECF7FD, 20); } }
	&__btn {
		position: absolute;
		top: calc(50% - 21px);
		z-index: 1;
		background-color: transparent;
		@include trs; }
	&__btn_prev {
		left: 15px;
		display: none!important; }
	//&__btn_prev[aria-disabled='true']
	//	opacity: 0
	//	visibility: hidden
	&__btn_prev &__icon {
		@include rotate(180); }
	&__btn_next {
		right: 15px; }
	//&__btn_prev[aria-disabled='false'] ~ &__btn_next[aria-disabled='false']
	//	opacity: 0
	//	visibility: hidden
	&__btn:hover &__icon {
		fill: #fff; }
	&__icon {
		font-size: 42px;
		fill: #fff;
		stroke: #ADAEAD;
		stroke-width: 10px;
		@include trs; }

	&_home {
		padding: 5px 0 15px; } }

// custom slick
.slick {
	&-dots {
		margin: -30px 0 0;
		text-align: center;
		@include hide-text;
		@include md {
			margin-top: -25px; }
		li {
			position: relative;
			border-bottom: 1px solid #666666;
			display: inline-block;
			vertical-align: middle;
			margin-right: 20px;
			height: 6px;
			width: 6px;
			border-radius: 15px;
			z-index: 3;
			background-color: #fff;
			cursor: pointer;
			&:last-child {
				margin-right: 0; } }
		button {
			@include hide-text; }
		li.slick-active {
			width: 14px;
			height: 14px;
			background-color: #BABABA; }
		li.slick-active + li {
			width: 11px;
			height: 11px;
			border-radius: 15px;
			@include md {
				width: 7px;
				height: 8px; } } } }
